<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-col
      v-if="loaded"
      cols="12"
    >
      <base-material-card
        color="primary"
        inline
        icon="mdi-mail"
        title="Mail"
        class="px-5 py-3"
      >
        <v-card-title>
          <v-btn
            color="primary"
            @click="create"
          >
            <v-icon small>
              mdi-account-plus
            </v-icon>Create
          </v-btn>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="organization"
            :search="search"
          >
            <template v-slot:item.action="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
              >
                mdi-pencil
              </v-icon>
            </template>
            <template v-slot:no-results>
              <v-alert
                :value="true"
                color="error"
                icon="warning"
              >
                Your search for "{{ search }}" found no results.
              </v-alert>
            </template>
          </v-data-table>
        </v-card-text>
      </base-material-card>
    </v-col>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
      loaded: false,
      search: '',
      headers: [
        { text: 'Name', value: 'name' },
        { text: 'Fuel Office API', value: 'fo_api_code' },
        { text: 'Payment type', value: 'payment_type' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      organization: [],
    }),
    mounted: function () {
      this.$http.get('/portal/organization')
        .then(
          (response) => {
            this.organization = response.data
            this.loaded = true
          }
        )
    },
    methods: {
      create () {
        this.$router.push({ name: 'organization_create' })
      },
      editItem (item) {
        this.$router.push({ name: 'organization_edit', params: { id: item.id } })
      },
    },
  }
</script>
